import React, { useState, useEffect } from 'react'
import { connect } from 'react-redux'
import { initializeSchoolStats } from '../../reducers/schoolStatsReducer'
import { setNotification } from '../../reducers/notificationReducer'

import { Container, Row, Col } from 'react-bootstrap'
import LoadingIndicator from '../common/LoadingIndicator'
import SchoolStatsTable from '../common/SchoolStatsTable'

const SchoolOverview = ({ schoolStats, initializeSchoolStats, setNotification, history }) => {

	const [isLoading, setIsLoading] = useState(true)

	useEffect(() => {
		// check if redirected from users
		const { action, location } = history
		if (action === 'REPLACE'
			&& location.state.from.pathname === '/school/users') {
			setNotification({
				message: '     ,    .',
				variant: 'warning'
			}, 10)
		}

		initializeSchoolStats()
			.catch(error => {
				setNotification({
					message: `   ,  :
						${error.status} ${error.statusText}`,
					variant: 'danger'
				}, 5)
			})
			.finally(() => setIsLoading(false))
	}, [history, setNotification, initializeSchoolStats])

	return (
		<Container>
			<Row className="d-flex justify-content-center">
				<Col md={8}>
					<p className="pt-3">
						 ,   ,    .
					</p>
					{isLoading
						? <LoadingIndicator
							animation="border"
							variant="primary"
						/>
						: <>
							<SchoolStatsTable
								link="/school/schoolclasses"
								linkText={`: ${schoolStats.schoolClasses.length} .`}
								header1stCol=""
								header2ndCol=""
								fieldName="title"
								stats={schoolStats.schoolClasses}
							/>
							<SchoolStatsTable
								link="/school/teachers"
								linkText={`: ${schoolStats.teachers.length} .`}
								header1stCol=""
								header2ndCol="&apos;"
								fieldName="name"
								stats={schoolStats.teachers}
							/>
							<SchoolStatsTable
								link="/school/pupils"
								linkText={`: ${schoolStats.pupils.length} .`}
								header1stCol=""
								header2ndCol="&apos;"
								fieldName="name"
								stats={schoolStats.pupils}
							/>
							<SchoolStatsTable
								link="/school/specialties"
								linkText={`: ${schoolStats.specialties.length} .`}
								header1stCol=""
								header2ndCol=""
								fieldName="title"
								stats={schoolStats.specialties}
							/>
						</>
					}
				</Col>
			</Row>
		</Container>
	)
}

const mapStateToProps = (state) => {
	return {
		schoolStats: state.schoolStats
	}
}

const mapDispatchToProps = {
	initializeSchoolStats,
	setNotification
}

// const MemodSchoolOverview = React.memo(SchoolOverview)

export default connect(
	mapStateToProps,
	mapDispatchToProps
)(SchoolOverview)
